.header{
  width: 100%;
  height: 50px;
  background: DodgerBlue;
  box-sizing: border-box;
}

.header h3{
  margin: 0;
  padding: 0.7em;
  color:#FFFFFF;
}
#content{
  display: block;
}
.content h2{
  color: maroon;
}
.content button{
  background: orange;
  color: #000000;
  border-radius: 1em;
  font-weight: bold;
  border: 1px solid #FFFFFF;
  padding: 0.5em;
  font-size: 1em;
  outline: none;
  cursor: pointer;
}
.content button.cBtn{
  background: greenyellow;
  color: #000000;
  border-radius: 1em;
  font-weight: bold;
  border: 1px solid #FFFFFF;
  padding: 0.5em;
  font-size: 1em;
  outline: none;
}

.content button.CheckBtn{
  float:right; 
  background: maroon;
  color: #FFFFFF;
}

.CartBox{
  border: 1px solid #CCC;
  margin: 2em;
  padding: 2em;
}
.orderDesc{
  border-right: 1px dashed #CCCCCC;
  float:left;
  width:35%;
  padding: 2em;
}

.orderDesc h4{
  margin: 0;
  display: block;
  text-decoration: underline;
}

.orderDesc ul{
  display: inline-block;
}

.order li{
  list-style-type: none;
}

.orderForm input{
  display: block;
}
.orderForm textarea{
  display: block;
}

.orderForm input[type="submit"]
{
  background: orange;
  color: #000000;
  border-radius: 1em;
  font-weight: bold;
  border: 1px solid #FFFFFF;
  padding: 0.5em;
  font-size: 1em;
  outline: none;
  cursor: pointer;
}

ul.Tmenu1{
  background:burlywood;
  margin: 0;
  padding: 1em;
}

ul.Tmenu1 li{
  display: inline;
  list-style-type: none;
  padding: 1em;
  text-decoration: none;
  font-weight: bold;
}

ul.Tmenu1 li a{
  color: #000000;
  text-decoration: none;
}

.Ingredients{
  margin: 1em;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 50%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  padding: 2em;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
.modal-main ul li{
  list-style-type:none;
}
.Cindicator{
  background:DodgerBlue;
  color:#FFFFFF;
  border-radius: 1em;
  padding: 1px;
  margin: 2px;
}